.about {
    display: flex;
    .image{
        width: 280px;
        height: 280px;
        position: relative;
    }
    .right{
        margin: 0 0 0 20px;
        padding: 0;
    }
    .title{
        margin: 0;
        padding: 0;
        font-weight: bold;
        font-size: 24px;
        line-height: normal;
    }
    
    .text{
        margin: 0;
        padding: 0;
        line-height: 180%;
        word-break: break-all;
    }
    ul, ol {
      color: #3e3e41;
      border-top: solid #504c4c 1px;/*上のボーダー*/
      border-bottom: solid #504c4c 1px;/*下のボーダー*/
      padding: 0.5em 0 0.5em 1.5em;
    }
    
    ul li, ol li {
      line-height: 1.5;
      padding: 0.5em 0;
    }
  }
  
  .activity {
    .title{
      padding: 1rem 2rem;
      border-left: 5px solid #000;
      background: #f4f4f4;
      font-weight: bold;
      font-size: 24px;
      line-height: normal;
    }
    .sub{
      margin: 0 5 0 0px;
      padding: 0;
      font-size: 18px;
      line-height: normal;
    }
    .text{
      margin: 0 1em;
      padding: 0;
      line-height: 180%;
      word-break: break-all;
  }
    ul li, ol li {
      margin: 0 1em;
      // list-style: none;
      line-height: 1;
      padding: 0.5em 0;
    }
  }